import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import './App.css';
import SplashScreen from './components/SplashScreen'
import ContactMeScreen from './components/ContactMeScreen'
import RecipeScreen from './components/RecipeScreen'

class App extends Component {
  render() {
    return (
      <Router>
        <Route exact={true} path="/" component={SplashScreen}></Route>
        <Route exact={true} path="/contact" component={ContactMeScreen}></Route>
        <Route exact={true} path="/recipes" component={RecipeScreen}></Route>
      </Router>
    );
  }
}

export default App;
